@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@200;300;400;500;600;700;800&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	background-color: #00020f;
}

#root {
	width: 100%;
}

#root::after {
	position: absolute;
	content: '';
	right: 0;
	width: 82%;
	height: 81vh;
	z-index: -1;
	top: 0;
	background: linear-gradient(to top, #00020f /*  rgb(0, 0, 0) */ 5%, transparent),
		linear-gradient(to right, #00020f /*  rgb(0, 0, 0) */ 1%, transparent);
}

.movie-bg {
	position: absolute;
	right: 0;
	z-index: -3;
	width: 80%;
	transition: 1.5s opacity ease-in;
	height: 80vh;
}

.movie-bg-2 {
	position: absolute;
	right: 0;
	z-index: -2;
	width: 80%;
	transition: 1.5s opacity ease-in;
	height: 80vh;
}

.all-elements {
	position: relative;
	width: 100%;
	height: 100vh;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	transition: 1s all cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.my-movie-list-cont {
	display: flex;
	flex-direction: column;
	align-self: center;
	position: absolute;
	z-index: 8000;
	right: -100rem;
	top: 0;
	height: 100vh;
	color: white;
	opacity: 1;
	width: 30%;
	background-color: #00041bef;
	transition: 0.5s all ease-in-out;
}

.close-btn {
	cursor: pointer;
	position: absolute;
	top: 1.5rem;
	right: 0.3rem;
	width: 30%;
	font-size: 1.5rem;
	color: white;
	z-index: 2000;
	transition: 0.2s all ease-in-out;
}

.close-btn:hover {
	transform: scale(1.5);
	color: red;
}

.my-list-title {
	margin: 20px auto;
	font-weight: 200;
	font-size: 1.5rem;
	font-family: var(--myfont);
}

.my-list-all-movies {
	width: 100%;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	align-items: center;
	overflow: scroll;
}

.my-list-all-movies::-webkit-scrollbar {
	height: 0px;
	width: 0px;
}

.each-movie-poster {
	cursor: pointer;
	width: 140px;
	margin: 1rem;
	transition: 0.5s transform cubic-bezier(0.19, 1, 0.22, 1);
}

.each-movie-poster:hover {
	transform: scale(1.07);
	box-shadow: 0 0 12px rgb(224, 8, 224, 0.5);
}

.my-list-btn-cont {
	width: 100%;
	height: 100px;
	margin: 10px 0;
}

.clear-list {
	position: absolute;
	bottom: 1.5rem;
	left: 50%;
	transform: translateX(-50%);
	cursor: pointer;
	font-family: var(--myfont);
	font-weight: 500;
	font-size: 1rem;
	color: white;
	width: 50%;
	padding: 7px 10px;
	background: #00020f;
	border: 1px solid white;
	border-radius: 5px;
	transition: 0.5s all cubic-bezier(0.19, 1, 0.22, 1);
	outline: none;
}

.clear-list:hover {
	background-color: white;
	color: #00020f;
}

.footer {
	color: white;
	position: relative;
	bottom: 0;
	z-index: 5000;
	display: flex;
	justify-content: center;
	width: 100%;
	background-color: #00020f;
	font-size: 1rem;
	margin-top: 4px;
	font-family: var(--myfont);
	padding-bottom: 7px;
	padding-top: 1px;
}

.footer > a {
	padding: 0 7px;
	color: goldenrod;
	margin-top: -1px;
}

/* 
issues: 
constant (2-3 clicks in 4 seconds) super fast clicking on different movies results in expensive api calls so api rejects the rqeuests
*/

* {
	--myfont: 'Heebo', sans-serif;
}

.nav-bar {
	display: flex;
	width: 93%;
	color: white;
}

.logo {
	width: 150px;
	font-family: var(--myfont);
	font-size: 2rem;
	margin-left: 10px;
	margin-top: 20px;
}

.my-watchlist-cont {
	font-family: var(--myfont);
	display: flex;
	align-items: center;
	margin-left: 20px;
	margin-right: 10px;
	margin-top: 20px;
	cursor: pointer;
}

.watch-list-icon {
	font-size: 2.5rem;
}

.watchlist-text {
	font-size: 1.5rem;
	margin-left: 10px;
	margin-top: 5px;
}

.searchbox-cont {
	position: relative;
	z-index: 1000;
	width: 100%;
	padding-top: 20px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-family: var(--myfont);
	margin: 0 auto;
}

.input-cont {
	position: relative;
	margin-top: 1rem;
	margin: 0 auto;
	width: 100%;
	display: flex;
	justify-content: center;
	transition: 1s all ease-in-out;
}

.search-icon {
	position: absolute;
	top: 11px;
	left: 12px;
	font-size: 1.3rem;
	color: rgba(255, 255, 255, 0.2);
	transition: 0.3s all ease-in-out;
}

.searchbox {
	width: 100%;
	padding: 8px 5px;
	padding-left: 2.5rem;
	border: 1px solid rgba(255, 255, 255, 0.1);
	outline: none;
	font-size: 1.3rem;
	border-radius: 5px;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
	background: rgba(255, 255, 255, 0.1);
	color: black;
	transition: 0.3s all ease-in-out;
}

.searchbox:focus {
	background: white;
	border: 1px solid white;
}

.input-cont:focus-within > .search-icon {
	color: #00020f;
}

.suggestion-cont {
	position: absolute;
	z-index: 3;
	top: 68px;
	overflow: hidden;
	font-size: 1.3rem;
	background-color: rgb(0, 0, 0, 0.8);
	border-top: none;
	width: 100%;
	color: white;
}

.suggestions-list-movie {
	cursor: pointer;
	padding: 10px 1rem;
}

.suggestions-list-movie:hover {
	background-color: rgb(255, 255, 255, 0.3);
}

.suggestion-img-cont-wrapper {
	height: 35%;
	position: relative;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	padding: 18px 0;
	transition: 1s height ease-in-out;
	margin-top: -5px;
	margin-bottom: 4px;
}

.all-display-movie-cont {
	position: relative;
	height: 100%;
	width: 93%;
	z-index: 10;
	bottom: -40rem;
	display: flex;
	justify-content: space-evenly;
	transition: 1.5s all cubic-bezier(0.5, 0.25, 0, 1);
	margin-bottom: 10px;
}

.suggested-title {
	color: white;
	font-family: var(--myfont);
	font-weight: 500;
	font-size: 1.1rem;
	margin-top: 10px;
	width: 91%;
}

.suggested-movie {
	cursor: pointer;
	margin: 0 10px;
	width: 10vw;
	height: 15vw;
	object-fit: contain;
	transition: 0.2s all ease-in-out, 0.3s transform cubic-bezier(0.5, 0.25, 0, 1);
}

.suggested-movie:hover {
	transform: scale(1.08);
	box-shadow: 0 0 12px rgb(224, 8, 224, 0.5);
}

.main-movie-cont {
	margin: 0 auto;
	margin-top: 15px;
	z-index: 10;
	width: 93%;
	display: flex;
	color: white;
}

.left-main-movie-poster {
	width: 210px;
	outline: none;
	transition: 1s all ease-in-out;
}

.main-poster {
	width: 100%;
	height: 280px;
	object-fit: contain;
	opacity: 0;
	transition: 1s height cubic-bezier(0.165, 0.84, 0.44, 1), 1s opacity ease-in-out;
}

.right-main-movie-details {
	position: relative;
	width: 100%;
	margin-left: 1.5rem;
	width: 40%;
	font-family: var(--myfont);
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	opacity: 0;
	transition: 0.8s opacity ease-in-out;
}

.main-movie-title {
	font-size: 2.5rem;
	font-weight: 500;
	margin-bottom: 10px;
	line-height: 2.7rem;
	transition: 1s all cubic-bezier(0.39, 0.575, 0.565, 1);
}

.main-movie-des {
	font-weight: 300;
	font-size: 1.1rem;
	margin-bottom: 15px;
}

.main-movie-details {
	margin-bottom: 10px;
}

.more-des {
	display: none;
}

.read-more {
	color: grey;
	cursor: pointer;
	transition: 0.4s color ease-in-out;
}

.read-more:hover {
	color: goldenrod;
}

.add-to-list {
	position: relative;
	margin-top: 5px;
}

#add-to-list-icon {
	position: absolute;
	font-size: 1.5rem;
	color: rgba(255, 255, 255, 0.3);
	margin-top: -5px;
	cursor: pointer;
	outline: none;
	transition: 0.3s all ease-in-out;
}

#add-to-list-input {
	display: none;
}

#add-to-list-icon:hover {
	color: rgba(255, 93, 93, 1);
}

#add-to-list-input:checked + #add-to-list-icon {
	color: red;
	-webkit-animation: heart 1s cubic-bezier(0.17, 0.89, 0.32, 1.49);
	        animation: heart 1s cubic-bezier(0.17, 0.89, 0.32, 1.49);
}

@-webkit-keyframes heart {
	0%,
	17.5% {
		transform: scale(1.3);
	}
}

@keyframes heart {
	0%,
	17.5% {
		transform: scale(1.3);
	}
}

.add-to-list-title {
	pointer-events: none;
	font-size: 1.2rem;
	margin-left: 35px;
	font-weight: 200;
}

.fa-star {
	font-size: 1rem;
	margin-right: 5px;
	color: gold;
}

/* media quesreies */

@media screen and (width: 1280px) and (height: 800px) {
	.left-main-movie-poster {
		width: 280px;
		margin-left: -10px;
	}
	.suggestion-img-cont-wrapper {
		height: 28%;
	}

	.main-poster {
		height: 350px;
	}
}

@media screen and (width: 1440px) and (height: 900px) {
	.left-main-movie-poster {
		width: 350px;
		margin-left: -25px;
	}

	.suggestion-img-cont-wrapper {
		height: 28%;
	}

	.main-poster {
		height: 420px;
	}

	.suggested-movie {
		width: 10.2vw;
		height: 15.5vw;
	}
}

@media screen and (width: 2560px) and (height: 1080px) {
	.left-main-movie-poster {
		width: 350px;
		margin-left: -10px;
	}

	.main-poster {
		height: 420px;
	}

	.suggestion-img-cont-wrapper {
		height: 40%;
	}

	.suggested-movie {
		width: 10vw;
		height: 15.5vw;
	}
}

